





































import Vue from "vue";

export default Vue.extend({
  name: "comments",
  props: {
    comments: Array,
    depth: {
      type: Number,
      default: 0
    }
  },
  computed: {
    style() {
      let offset = Number(this.depth) * 5;
      return "padding-left: " + offset + "vw";
    }
  }
});
